.MovieDetails {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1000;
    background-color: white;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-transform: translateX(-105vw);
        -ms-transform: translateX(-105vw);
            transform: translateX(-105vw);
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.MovieDetails .hero-img {
    position: relative;
    width: 100%;
    height: 520px;
    background-color: #aaa;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    z-index: 1001;
}

.MovieDetails .hero-img .overlay {
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.5;
}

.MovieDetails .bottom-gradient {
    position: relative;
    z-index: 1002;
    width: 100%;
    height: 320px;
    margin-top: -316px;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+94&0+0,1+79 */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 79%,rgba(255,255,255,1) 94%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0)),color-stop(79%, rgba(255,255,255,1)),color-stop(94%, rgba(255,255,255,1)));
    background: -o-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 79%,rgba(255,255,255,1) 94%);
    background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 79%,rgba(255,255,255,1) 94%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */
}

.MovieDetails .movie-details-container {
    position: relative;
    padding: 20px;
    z-index: 1003;
    margin-top: -520px;
}

.MovieDetails .go-back-icon-container {
    margin-bottom: 20px;
}

.MovieDetails .go-back-icon-container .go-back-icon {
    background: transparent;
    border: none;
    outline: none;
    padding: 0;
    margin: 0;
    width: 36px;
    height: 36px;
    padding-right: 6px;
    cursor: pointer;
}


.MovieDetails .go-back-icon-container .go-back-icon svg {
    width: 100%;
    display: block;
    fill: white;
}

.MovieDetails .go-back-icon-container.bottom {
    text-align: right;
}

.MovieDetails .go-back-icon-container.bottom .go-back-icon svg {
    fill: black;
}

.MovieDetails .movie-poster {
    width: 100%;
    height: 134vw;
    background-color: #999;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    border: 6px solid white;
    -webkit-box-shadow: 0 0 2px 1px #ccc;
            box-shadow: 0 0 2px 1px #ccc;
}

.MovieDetails .movie-title {
    font-size: 1.7rem;
    color: #333;
    font-weight: bold;
    word-wrap: break-word;
    margin-top: 20px;
    letter-spacing: 0.01ch;
}

.MovieDetails .movie-genres {
    font-size: 0.9rem;
    color: magenta;
    font-weight: bold;
    word-wrap: break-word;
    margin-top: 10px;
    letter-spacing: 0.1ch;
}

.MovieDetails .movie-genres span {
    margin-right: 12px;
    display: inline-block;
}

.MovieDetails .misc-details {
    font-size: 0.9rem;
    color: #333;
    word-wrap: break-word;
    margin-top: 25px;
    letter-spacing: 0.1ch;
    line-height: 2.5ch;
}

@media (min-width: 460px) {
    .MovieDetails .movie-poster {
        width: 360px;
        height: 540px;
    }    
    
    .MovieDetails .movie-details-container {
        margin-left: 50%;
        -webkit-transform: translateX(-50%);
            -ms-transform: translateX(-50%);
                transform: translateX(-50%);
        width: 402px;
    }
}



